.heder__top {
	display: flex;
	justify-content: space-between;
	margin: 21px 0 23px;
	padding: 0 15px 0 35px;
	line-height: 135%;
}

.heder__phone {
	text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
	& a {
		color: $dark;
		margin-left: 10px;
	}
}

.heder__menu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 15px 0 35px;
	height: 80px;
	background-color: #fff;
	border-radius: 28px;
}

.heder__logo-descr {
	margin-top: 3px;
	font-size: 10px;
	color: $malachite;
}
//nav/////////////////////////////////////
.nav {
	& ul {
		display: flex;
		align-items: center;
		column-gap: 25px;
	}
	& a {
		color: $dark;
		transition: color 0.3s ease-in-out;
	}
	& a:hover {
		color: $malachite;
	}
}

.burger {
	position: relative;
	z-index: 101;
	width: 30px;
	height: 20px;
	display: none;
	border: none;
	padding: 0;
	background-color: transparent;
	cursor: pointer;
	&__line {
		position: absolute;
		height: 2px;
		width: 100%;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		background-color: $dark;
		transition: opacity 0.3s ease-in-out;
	}
	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 0;
		width: 100%;
		height: 2px;
		background-color: $dark;
		transition:
			transform 0.3s ease-in-out,
			top 0.3s ease-in-out,
			bottom 0.3s ease-in-out;
	}
	&::before {
		top: 0;
	}
	&::after {
		bottom: 0;
	}
}

.burger--active .burger__line {
	opacity: 0;
}

.burger--active::before {
	top: 50%;
	transform: rotate(45deg);
}

.burger--active::after {
	bottom: auto;
	top: 50%;
	transform: rotate(-45deg);
}

.stop--scroll {
	overflow: hidden;
}

@media (max-width: 1024px) {
	.nav {
		position: fixed;
		z-index: 100;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vh;
		overflow-y: auto;
		padding: 88px 10px 30px 30px;
		background-color: rgba(255, 255, 255, 0.9);
		transform: translateX(-100%);
		transition: transform 0.6s ease-in-out;
		& ul {
			flex-direction: column;
			align-items: flex-end;
		}
		& li {
			margin-bottom: 30px;
			margin-right: 20px;
			font-size: 25px;
		}
		& li:not(:last-child) {
			margin-right: 20px;
		}
		&::after{
			content: "";
			position: absolute;
			left: 50px;
			top: 20px;
			width: 150px;
			height: 27px;
			background-image: url(../img/logo.svg);
		}
	}
	.nav--visible {
		transform: none;
	}
	.burger {
		display: block;
	}
	.toggle {
		display: none;
	}
	.heder__top{
		display: none;
	}
}
//nav/////////////////////////////////////
//toggle/////////////////////////////////////
.toggle {
	cursor: pointer;
	display: inline-block;
}

.toggle-switch {
	display: inline-block;
	background: #ccc;
	border-radius: 16px;
	width: 58px;
	height: 32px;
	position: relative;
	vertical-align: middle;
	transition: background 0.3s;
}
.toggle-switch:before,
.toggle-switch:after {
	content: "";
}
.toggle-switch:before {
	display: block;
	background: linear-gradient(to bottom, #fff 0%, #eee 100%);
	border-radius: 50%;
	width: 24px;
	height: 24px;
	position: absolute;
	top: 4px;
	left: 4px;
	transition: left 0.3s;
}
.toggle:hover .toggle-switch:before {
	background: linear-gradient(to bottom, #fff 0%, #fff 100%);
}
.toggle-checkbox:checked + .toggle-switch {
	background: #00ce90;
}
.toggle-checkbox:checked + .toggle-switch:before {
	left: 30px;
}

.toggle-checkbox {
	position: absolute;
	visibility: hidden;
}

.toggle-label {
	margin-right: 6px;
	position: relative;
	top: 2px;
}
//toggle/////////////////////////////////////
.heder__anons {
	display: flex;
	justify-content: space-between;
	column-gap: 40px;
	margin-top: 8px;
	background-color: #fff;
	border-radius: 28px;
	padding: 35px 15px 20px 35px;
}

.heder__text {
	display: flex;
	flex-direction: column;
}

.heder__title {
	font-weight: 700;
	font-size: clamp(18px, 2.5vw, 38px);
	line-height: 113%;
	color: $dark;
	& span {
		color: $malachite;
	}
}

.heder__descr {
	max-width: 526px;
	margin-top: 30px;
	font-size: clamp(10px, 2.5vw, 18px);
	line-height: 150%;
	color: $dark;
}

.heder__price {
	position: relative;
	margin-top: 30px;
	width: 253px;
	padding: 25px 85px 25px 25px;
	color: #fff;
	background-color: $malachite;
	border-radius: 114px;
	font-size: 17px;
	transition: background-color ease-in-out 0.3s;
	&::after {
		content: "";
		position: absolute;
		right: 5px;
		top: 50%;
		transform: translateY(-50%);
		width: 52px;
		height: 52px;
		background-color: #34d8a6;
		border-radius: 50%;
		transition: background-color ease-in-out 0.3s;
	}
	&::before {
		content: "";
		position: absolute;
		z-index: 5;
		right: 21px;
		top: 50%;
		transform: translateY(-50%);
		width: 16px;
		height: 16px;
		background-image: url(../img/icons/arrow.svg);
		background-repeat: no-repeat;
		object-fit: contain;
	}
	&:hover {
		background-color: #34d8a6;
	}
	&:hover:after {
		background-color: $malachite;
	}
}

.heder__img {
	position: relative;
	flex: 0 0 447px;
	height: 499px;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}

@media (max-width: 961px) {
	.heder__menu {
		border-radius: 14px;
	}
	.heder__title{
		font-size: 24px;
	}
	.heder__anons {
		column-gap: 30px;
		padding: 40px 15px 20px 35px;
		border-radius: 14px;
	}
	.heder__text{
		flex-shrink: 0;
	}
	.heder__descr {
		max-width: 380px;
		margin-top: 30px;
		font-size: 14px;
		line-height: 150%;
		color: $dark;
	}
	.heder__img {
		flex: 0 1 300px;
		height: 260px;
	}
}

@media (max-width: 680px) {
	.heder__anons {
		flex-direction: column;
		position: relative;
	}
	.heder__descr {
		max-width: 680px;
		margin-top: 17px;
	}
	.heder__img {
		margin-top: 35px;
	}
}
